.daily-weather {
  max-width: 90vw;
  margin: 3vmin;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 1022px) {
  .daily-weather {
    height: 65vmin;
    overflow-x: scroll;
    white-space: nowrap;
    flex-direction: row;
  }
  .daily-weather-container {
    margin-top: 6vmin;
  }
}
